"use client";

import { useState } from "react";
import { Button } from "./ui/button";
import { Input } from "./ui/input";
import { Label } from "./ui/label";

export default function Header() {
  const [showPopup, setShowPopup] = useState(false);

  const handleLoginClick = () => {
    setShowPopup(true);
  };

  const closePopup = () => {
    setShowPopup(false);
  };

  return (
    <>
      <header className="bg-white shadow-md">
        <div className="container mx-auto flex items-center justify-between py-4 px-6">
          {/* Logo */}
          <div className="text-blue-600 text-2xl font-bold">
            <Button asChild variant="link">
              <a href="/">Logo</a>
            </Button>
          </div>

          {/* Navigation Links */}
          <nav className="hidden md:flex space-x-6">
            <Button asChild variant="link">
              <a href="/">Home</a>
            </Button>
            <Button asChild variant="link">
              <a href="/upcoming-marathons">Upcoming Marathons</a>
            </Button>
            <Button asChild variant="link">
              <a href="/how-it-works">How It Works</a>
            </Button>
            <Button asChild variant="link">
              <a href="/contact-us">Contact Us</a>
            </Button>
          </nav>

          <div className="flex items-center space-x-4">
            {/* Login Button */}
            <Button variant="ghost" onClick={handleLoginClick}>
              Login
            </Button>

            {/* Register Button */}
            <Button asChild className="bg-green-600">
              <a href="/register">Register</a>
            </Button>
          </div>
        </div>
      </header>

      {/* Popup */}
      {showPopup && (
        <div className="fixed inset-0 flex items-center justify-center bg-black bg-opacity-50 z-50">
          <div className="bg-white rounded-lg p-8 shadow-lg w-96">
            <h2 className="text-xl font-bold mb-4">Login</h2>
            <p className="text-gray-600 mb-4">
              Enter your login and password to continue.
            </p>
            <form className="space-y-4">
              {/* Email Input */}
              <div>
                <Label htmlFor="email">Email</Label>
                <Input
                  type="email"
                  id="email"
                  placeholder="Enter your email"
                />
              </div>

              {/* Password Input */}
              <div>
                <Label htmlFor="password">Password</Label>
                <Input
                  type="password"
                  id="password"
                  placeholder="Enter your password"
                />
              </div>

              {/* Login Button */}
              <Button className="bg-green-600 w-full">Login</Button>
            </form>

            {/* Close Button */}
            <Button variant="ghost" className="w-full mt-4" onClick={closePopup}>
              Close
            </Button>
          </div>
        </div>
      )}
    </>
  );
}